// 1200; 992; 768; 576; 425;

// @include r(768) {}
@mixin r($width) {
  @media (max-width: #{$width}px) {  
    @content;
   }
}

@mixin rm($width) {
  @media (min-width: #{$width}px) {
    @content;
   }
}

@mixin rh($height) {
  @media (max-height: #{$height}px) {
    @content;
   }
}
