html {
   box-sizing: border-box;
}

*,
*::after,
*::before {
   box-sizing: inherit;
}

ul,
ol {
   padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
   margin: 0;
}

ul {
   list-style: none;
}

img {
   width: 100%;
   display: block;
}

input,
button,
textarea,
select {
   font: inherit;
}

body {
   min-height: 100vh;
}

a {
   text-decoration: none;
   color: inherit;
}